export default {
  data: function data() {
    return {
      baseURL: process.env.VUE_APP_BASEURL,
      loading: false,
      all: false,
      items: [],
      multipleSelection: [],
      changeCartTime: 0
    };
  },
  computed: {
    isAll: function isAll() {
      return this.items.length && this.multipleSelection.length == this.items.length;
    },
    totalAmount: function totalAmount() {
      var amount = 0;
      this.multipleSelection.forEach(function (e) {
        amount += Number(e.subtotal);
      });
      return Number(amount).toFixed(2);
    }
  },
  watch: {
    isAll: function isAll() {
      this.all = this.isAll;
    }
  },
  mounted: function mounted() {
    this.$Burying({
      object: '13001'
    });
    this.getCart();
  },
  methods: {
    productClick: function productClick(id) {
      // 查看商品详情
      this.$Burying({
        object: '7002',
        objectId: id
      });
    },
    toPlaceOrder: function toPlaceOrder() {
      if (JSON.parse(localStorage.getItem('c_ourMallUserInfo')).verifyEmail == '2') {
        this.$emit('openVerify');
        return;
      }

      this.$Burying({
        object: '13007'
      }); //下订单

      this.$router.push({
        name: 'placeOrder',
        params: {
          placeOrderArr: this.multipleSelection
        }
      });
    },
    checkAll: function checkAll(val) {
      var _this = this;

      this.items.forEach(function (e) {
        _this.$refs.cartTable.toggleRowSelection(e, val);
      });
    },
    handleSelectionChange: function handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    delSelect: function delSelect() {
      var _this2 = this;

      //批量删除
      this.$confirm('Do you want to delete selected items?', '', {
        confirmButtonClass: " el-button--danger",
        type: "error"
      }).then(function () {
        var stockIds = _this2.multipleSelection.map(function (e) {
          return e.stockId;
        });

        _this2.loading = true;

        _this2.$apiCall('api.Cart.deleteCart', {
          stockIds: stockIds
        }, function (r) {
          if (r.ErrorCode == "9999") {
            _this2.getCart();
          } else {
            _this2.$elementMessage(r.Message, "error");
          }
        });
      }).catch(function () {});
    },
    delCart: function delCart(item, index) {
      var _this3 = this;

      item.delLoading = true;
      this.$Burying({
        object: '13004',
        objectId: item.stockId
      });
      this.$apiCall('api.Cart.deleteCart', {
        stockId: item.stockId
      }, function (r) {
        if (r.ErrorCode == "9999") {
          _this3.items.splice(index, 1);
        } else {
          _this3.$set(item, 'delLoading', false);

          _this3.$elementMessage(r.Message, "error");
        }
      });
    },
    updCartQty: function updCartQty(item, index, oldValue) {
      var _this4 = this;

      // 修改购物车数量
      clearTimeout(this.changeCartTime);
      this.changeCartTime = setTimeout(function () {
        _this4.$Burying({
          object: "13003",
          objectId: item.stockId
        });

        _this4.$apiCall('api.Cart.updCartQuantity', {
          stockId: item.stockId,
          quantity: item.quantity
        }, function (r) {
          if (r.ErrorCode == "9999") {
            r.Data.Results.updCart.delLoading = false;

            _this4.items.splice(index, 1, r.Data.Results.updCart);

            _this4.$nextTick(function () {
              //改数据后任然选中
              _this4.$refs.cartTable.toggleRowSelection(_this4.items[index], true);
            });
          } else {
            _this4.$set(item, 'quantity', oldValue);

            _this4.$elementMessage(r.Message, "error");
          }
        });
      }, 500);
    },
    getCart: function getCart() {
      var _this5 = this;

      //初始获取订单
      this.loading = true;
      this.$apiCall("api.Cart.getCartList", {}, function (r) {
        _this5.loading = false;

        if (r.ErrorCode == "9999") {
          _this5.items = [];
          var results = r.Data.Results;
          Object.keys(results).forEach(function (e) {
            results[e].delLoading = false;

            _this5.items.push(results[e]);
          }); //开场全选

          _this5.items.forEach(function (e) {
            _this5.$refs.cartTable.toggleRowSelection(e, true);
          });
        } else {
          _this5.$elementMessage(r.Message, "error");
        }
      });
    },
    formatAttr: function formatAttr(attr) {
      attr = attr.split("||");
      attr = attr.filter(function (s) {
        return s && s.trim();
      });
      var str = attr.join(',');
      return str;
    }
  }
};